import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export const SPIN_BUTTON_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => SpinButtonComponent),
    multi: true
};

@Component({
    selector: 'ux-spin-button',
    template: `<button class="spin-button"
        *ngIf="spinners"
        tabindex="-1"
        [disabled]="disabled"
        [attr.aria-label]="incrementAriaLabel"
        [attr.aria-disabled]="disabled"
        (click)="triggerIncrement()">

  <span class="hpe-icon hpe-up"></span>
</button>

<input [type]="type"
       role="spinbutton"
       [min]="min"
       [max]="max"
       [tabindex]="0"
       class="form-control"
       [placeholder]="placeholder"
       [readOnly]="readOnly"
       [disabled]="disabled"
       [attr.aria-label]="inputAriaLabel"
       [attr.aria-disabled]="disabled"
       [attr.aria-valuemin]="min"
       [attr.aria-valuenow]="value"
       [attr.aria-valuemax]="max"
       [attr.aria-readonly]="readOnly"
       [ngModel]="value"
       (ngModelChange)="valueChange.emit($event)"
       (wheel)="scroll($event)"
       (keydown.arrowup)="arrowkeys ? triggerIncrement() : null; $event.preventDefault()"
       (keydown.arrowdown)="arrowkeys ? triggerDecrement() : null; $event.preventDefault()">

<button class="spin-button"
        *ngIf="spinners"
        tabindex="-1"
        [disabled]="disabled"
        [attr.aria-label]="decrementAriaLabel"
        [attr.aria-disabled]="disabled"
        (click)="triggerDecrement()">

  <span class="hpe-icon hpe-down"></span>
</button>`,
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    providers: [SPIN_BUTTON_VALUE_ACCESSOR]
})
export class SpinButtonComponent implements ControlValueAccessor {

    @Input() set value(value: any) {
        this._value = value;
        this.onChangeCallback(value);
        this.onTouchedCallback();
    }

    get value(): any {
        return this._value;
    }

    @Input() type: string = 'text';
    @Input() min: number;
    @Input() max: number;
    @Input() placeholder: string = '';
    @Input() disabled: boolean = false;
    @Input() spinners: boolean = true;
    @Input() readOnly: boolean = true;
    @Input() scrolling: boolean = true;
    @Input() arrowkeys: boolean = true;

    @Input() incrementAriaLabel: string;
    @Input() inputAriaLabel: string;
    @Input() decrementAriaLabel: string;

    @Output() valueChange = new EventEmitter<any>();

    @Output() increment = new EventEmitter<void>();
    @Output() decrement = new EventEmitter<void>();

    onTouchedCallback: () => void = () => { };
    onChangeCallback: (_: any) => void = () => { };

    private _value: any;

    scroll(event: WheelEvent): void {

        if (!this.scrolling) {
            return;
        }

        if (event.deltaY > 0) {
            this.triggerDecrement();
        } else {
            this.triggerIncrement();
        }

        event.preventDefault();
    }

    triggerIncrement(): void {
        if (!this.disabled) {
            this.increment.emit();
        }
    }

    triggerDecrement(): void {
        if (!this.disabled) {
            this.decrement.emit();
        }
    }

    writeValue(value: any): void {
        this.value = value;
    }

    registerOnChange(fn: (_: any) => void): void {
        this.onChangeCallback = fn;
    }

    registerOnTouched(fn: () => void): void {
        this.onTouchedCallback = fn;
    }

    setDisabledState(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

}